'use client';

import React, { useState } from 'react';
import Hero from '../components/Hero';
import Solutions from '../components/Solutions';
import RoiCalculator from '../components/RoiCalculator';
import Pricing from '../components/Pricing';
import Footer from '../components/Footer';
import ContactModal from '../components/ContactModal';
import { Building2, Menu, X, Phone } from '../components/Icons';

const Navbar = ({ onContact }) => {
  const [isOpen, setIsOpen] = useState(false);

  const handleMobileLinkClick = () => {
    setIsOpen(false);
  };

  return (
    <nav className="bg-primary-900 shadow-sm sticky top-0 z-40 border-b border-primary-800">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex">
            <div className="flex-shrink-0 flex items-center">
              <div className="h-8 w-8 border-2 border-accent-500 rounded-full flex items-center justify-center mr-2">
                 <span className="text-accent-500 font-bold text-[10px]">P-AI</span>
              </div>
              <span className="font-bold text-xl text-white tracking-widest">PISTLE AI</span>
            </div>
            <div className="hidden md:ml-6 md:flex md:space-x-8">
              <a href="#" className="border-accent-500 text-white inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium">
                Home
              </a>
              <a href="#solutions" className="border-transparent text-slate-300 hover:border-slate-300 hover:text-white inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium">
                Services
              </a>
              <a href="#calculator" className="border-transparent text-slate-300 hover:border-slate-300 hover:text-white inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium">
                ROI Calculator
              </a>
              <a href="#pricing" className="border-transparent text-slate-300 hover:border-slate-300 hover:text-white inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium">
                Pricing
              </a>
            </div>
          </div>
          <div className="hidden md:flex items-center">
            <button
              onClick={onContact}
              className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-primary-900 bg-accent-500 hover:bg-accent-400 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-accent-500 transition-colors"
            >
              <Phone className="h-4 w-4 mr-2" />
              Contact Us
            </button>
          </div>
          <div className="-mr-2 flex items-center md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="inline-flex items-center justify-center p-2 rounded-md text-slate-400 hover:text-white hover:bg-primary-800 focus:outline-none"
            >
              {isOpen ? <X className="block h-6 w-6" /> : <Menu className="block h-6 w-6" />}
            </button>
          </div>
        </div>
      </div>

      {isOpen && (
        <div className="md:hidden bg-primary-800 shadow-xl">
          <div className="pt-2 pb-3 space-y-1">
            <a href="#" onClick={handleMobileLinkClick} className="bg-primary-900 border-accent-500 text-white block pl-3 pr-4 py-2 border-l-4 text-base font-medium">Home</a>
            <a href="#solutions" onClick={handleMobileLinkClick} className="border-transparent text-slate-300 hover:bg-primary-700 hover:border-slate-300 hover:text-white block pl-3 pr-4 py-2 border-l-4 text-base font-medium">Services</a>
            <a href="#calculator" onClick={handleMobileLinkClick} className="border-transparent text-slate-300 hover:bg-primary-700 hover:border-slate-300 hover:text-white block pl-3 pr-4 py-2 border-l-4 text-base font-medium">ROI Calculator</a>
            <a href="#pricing" onClick={handleMobileLinkClick} className="border-transparent text-slate-300 hover:bg-primary-700 hover:border-slate-300 hover:text-white block pl-3 pr-4 py-2 border-l-4 text-base font-medium">Pricing</a>
            <button 
              onClick={() => { handleMobileLinkClick(); onContact(); }} 
              className="w-full text-left border-transparent text-accent-500 font-bold hover:bg-primary-700 block pl-3 pr-4 py-2 border-l-4 text-base"
            >
              Contact Us
            </button>
          </div>
        </div>
      )}
    </nav>
  );
};

export default function Home() {
  const [isContactOpen, setIsContactOpen] = useState(false);

  return (
    <div className="min-h-screen bg-slate-50">
      <Navbar onContact={() => setIsContactOpen(true)} />
      
      <main>
        <Hero onCtaClick={() => setIsContactOpen(true)} />
        <Solutions />
        <RoiCalculator />
        <Pricing onPlanSelect={() => setIsContactOpen(true)} />
      </main>

      <Footer />
      <ContactModal isOpen={isContactOpen} onClose={() => setIsContactOpen(false)} />
    </div>
  );
}
